import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { AddAreaService } from './../services/AddAreaService';
import { AddCategoryService } from './../services/AddCategoryService';
import { GetChemistDataService } from './../services/GetChemistDataService';
import { AddchemistService } from './../services/AddchemistService';
import {GetPopularChemistService} from './../services/GetPopularChemistService';
import { GetChemistDataModel} from './../models/GetChemistDataModel';
import { GetAllCitiesModel} from './../models/GetAllCitiesModel';
import { categoryModel} from './../models/categoryModel';
import { chemistModel} from './../models/chemistModel';
import {GetAreaNameModel} from './../models/GetAreaNameModel';
import{GetPopularChemistModel} from './../models/GetPopularChemistModel';

declare var jQuery:any;
@Component({
  selector: 'GetPopularChemist',
  providers: [GetPopularChemistService,AddchemistService,GetChemistDataService,AddAreaService],
  styles: [ require('./GetPopularChemist.style.scss') ],
  template: require('./GetPopularChemist.template.html'),
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'chemist-page app'
  },
})

export class GetPopularChemistComponent {
  router: Router;
  public SingleChemistDataModel:GetChemistDataModel[]=[];
  public UserType:any;
  public GetAllCitiesModelArray:GetAllCitiesModel[]=[];
  public ChemistObject:GetChemistDataModel[];
  public unconfirmedChemist:chemistModel;
  public GetAreaNameModelArray:GetAreaNameModel[]=[];
  public GetPopularChemistModelArray:GetPopularChemistModel[]=[];
  public ChemistName:any;
  public GetChemistId:any;
  public tempObj:any;
  public Address:any;
  public CityId:any;
  public FullName:any;
  public ChemistId:any;
  public ChemistEmail:any;
  public ChemistPassword:any;
  public Longitude:any;
  public RegularExpression :any;
  public Latitude:any;
  public PharmacyId:any;
  public City:any;
  public ProductType:any;
  public getArea_Id:any;

  constructor(public _getPopularChemistService:GetPopularChemistService, _AddAreaNameService:AddAreaService ,public _addChemistServiceService:AddchemistService,public _getChemistDataService:GetChemistDataService, router: Router) {
    this.UserType=localStorage.getItem("UserType");
    if(this.UserType==null)
    {
      this.router.navigate(["/app/login"]);
    
    }
    else{
    this.router = router;
   // this.SingleChemistDataModel.Chemist.PharmacyName=this.ChemistName;
   this.GetChemistId=localStorage.getItem("GetChemistId");
   this.ProductType=100;
   this._getPopularChemistService.GetPopularChemist().subscribe(
       response => {
         
        this.GetPopularChemistModelArray=response.data;
         console.log('Chemist name',this.GetPopularChemistModelArray);

         jQuery("#snackbar1").html(response.message);
         this.myFunction();
 this.getAllCities()
 
       });  
      }
  
  }

  getAllCities()
  {
    
    this._getChemistDataService.GetAllCitiesService().subscribe(
      response => {
       this.GetAllCitiesModelArray=response.data;
        console.log(this.GetAllCitiesModelArray);

        jQuery("#snackbar1").html(response.message);
        this.myFunction();
     
      });  

  }
  getAreaId(AreaId)
  {
this.getArea_Id=AreaId;
  }
  
  getCityName(cityId)
  {
 
  }
  getPharmacyId(PharmacyID)
{
this.PharmacyId=PharmacyID;
console.log(this.PharmacyId);
var obj = this.SingleChemistDataModel.find(x =>x.ChemistId == this.PharmacyId);
this.GetChemistId=obj.Chemist.Id;

localStorage.setItem("GetChemistId",obj.ChemistId.toString());
this.GetChemistId=localStorage.getItem("GetChemistId");

this.Address=obj.Chemist.Address;
this.FullName=obj.FullName;
console.log(obj);
console.log(this.Address);
console.log(this.FullName);
}
  ngOnInit(): void {
    

    setTimeout(function () {


      jQuery('#somecomponent').locationpicker({
        location: {
          latitude: 	30.3753,
          longitude: 69.3451
        },
        radius: 300,
        inputBinding: {
          latitudeInput: jQuery('#us7-lat'),
          longitudeInput: jQuery('#us7-lon'),
          radiusInput: jQuery('#us7-radius'),
          locationNameInput: jQuery('#us7-address'),
        },
        enableAutocomplete: true
      });
    }, 3000);
   
  }

  addChemist()
  { 
   
    this.Longitude=jQuery("#us7-lon").val();
    this.Latitude=jQuery("#us7-lat").val();
    console.log(this.Longitude);

    if(!this.ChemistEmail || !this.ChemistName || !this.ChemistPassword || !this.Longitude || !this.Latitude)
    {
      //alert("empty");
   
       jQuery("#snackbar1").html("Please enter Empty field");
      this.myFunction();  

    }
    else
    {
      
this.RegularExpression=/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
      if(this.RegularExpression.test(this.ChemistEmail))
      {
        this.Longitude=jQuery("#us7-lon").val();
        this.Latitude=jQuery("#us7-lat").val();
      this.ChemistId=localStorage.getItem("GetChemistId")
      console.log(this.ChemistId);
     this._addChemistServiceService.AddChemist(this.getArea_Id,this.ChemistEmail, this.CityId,this.ChemistName,this.ChemistPassword,this.Longitude,this.Latitude,this.PharmacyId).subscribe(
    response => {
     console.log(response.data);
     jQuery("#snackbar1").html("Add Chemist Successfully");
     this.myFunction();

    }); 
  }
  else
  {
    jQuery("#snackbar1").html("Email is not Valid");
    this.myFunction();

  }
  }
  }
  myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar1")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
}





}
  




